	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"Tired of maintaining your one-off script that has now become someone's job to execute? Wishing you could create polished applications on the command line similar to git or cucumber? In my talk, I'll talk about what makes a command line application \"awesome\", and why you should care. I'll talk about what makes Ruby particularly suited to this task over mainstays like bash and Perl. We'll compare and contrast several Ruby libraries that can make even your lowliest automation script a polished, maintainable, and predictable application.","meta":{"duration":"PT28M57S"},"presenter":{"name":null},"source":{"id":"1ILEw6Qca3U","provider":"youtube"},"slug":"make-awesome-command-line-apps-with-ruby-by-dave-copeland","tag":["rubymidwest2011","Ruby","CLI","Command-line Interface"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/1ILEw6Qca3U/default.jpg","height":90,"width":120}},"title":"Make awesome command line apps with ruby by Dave Copeland"}}]}},"pathContext":{"slug":"cli","tag":"CLI"}}

